* {
    box-sizing: border-box;
}

body {
    background-color: rgba(142, 154, 208, 0.481);
    font-family: sans-serif;
    margin: 0;    
}

.page_design {
    height: 100vh;
    display: grid;
    justify-content: center;
    grid-template-columns:repeat(2, minmax(40%,670px));
    grid-template-rows: 0.2fr 1fr 0.2fr;
    grid-template-areas: "header header"
                         "form  result"
                         "footer footer" ;
    gap: 0px 10px;
    }

.heading {
   grid-area: header;
   text-align: center;
   font-size: 3em;
   text-decoration: underline;
}
._titles {
    color:aliceblue;
    font-size: 2rem;
}

.form, .result {
    width: clamp(350px,100%,50vw);
    background-color: rgba(31, 40, 91, 0.481);
    grid-area:form ;
    text-align: center;
    color: rgb(242, 230, 230);
    margin: 0 auto;
    line-height: 2em;
    height: 100%;
    max-height: 500px;
}

.result {
    background-color: rgba(87, 134, 223, 0.481);
   grid-area: result;

}
  label {
        font-size: 1rem;
        display: block;
        margin-top: 1em;
    }
    
    input, select {
    width: clamp(5em,50%,15em);
    height: 2rem;
    border: 1px solid lightgray;
    border-radius: 5px;
    padding: 5px;
    margin-left: 1rem ;
    }

   Button {
       margin: 2rem !important;
   }


.footer {
    grid-area: footer;
    text-align: center;
    font-size: 1rem;
 }

.footer > p {
    margin: auto; /* this is centering the footer !! ??? */
    width: clamp(45ch, 50%, 75ch); 
}

.validation {
    color: rgb(49, 30, 142);
}



@media(max-width:786px) {
    .page_design {
        
    grid-template-columns:repeat(2, minmax(200px,670px));
    grid-template-rows: 0.2fr 1.1fr 0.2fr;
        grid-template-areas:
         "header header"
        "form form" 
        "result result"                  
        "footer footer";
        gap: 10px 10px;
    }
    
          .result{
              height: 300px;
              height: inherit;
          }
          .footer {
              text-align: center;
              margin: 1rem 1rem;
          }
}